import React, { useState, useEffect} from 'react';
import axios from 'axios';
import logo from './logo.svg';
import './App.css';

function App() {

    const [get_giveaway, set_giveaway] = useState([])

    useEffect(() => {
      axios({
        method: "GET",
        url: "http://127.0.0.1:8000/api/test-api"
      }).then(response => {
        set_giveaway(response.data)
      })
    }, [])

  return (
    <div className="App">
        <ul>
          {get_giveaway.map(p => (
            <li key={p.id}>{p.name}</li>
          ))}
        </ul>
    </div>
  );
}

export default App;
